import {
  Component, EventEmitter, Input, OnChanges, OnInit, Output, Pipe, PipeTransform,
  SimpleChanges
} from '@angular/core';
import { ContactService } from '../contact.service';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'app-contacts',
  templateUrl: './contacts.component.html',
  styleUrls: ['./contacts.component.css']
})
export class ContactsComponent implements OnInit {

  @Output() onContactSelect = new EventEmitter<any>();
  favoriteContacts: any = [];
  otherContacts: any = [];

  constructor(private contactService: ContactService) { }

  ngOnInit(): void {
    this.contactService.getContacts().forEach((contact) => {
      if (contact.isFavorite) {
        this.favoriteContacts.push(contact);
      } else {
        this.otherContacts.push(contact);
      }
    });
  }

  contactSelect(contact: any) {
    this.onContactSelect.emit(contact);
  }

}

@Pipe({
  name: 'sortBy'
})
export class SortByPipe implements PipeTransform {
  transform(arr: Array<any>, prop: any, reverse: boolean = false): any {
    if (arr === undefined) { return; }
    const m = reverse ? -1 : 1;
    return arr.sort((a: any, b: any): number => {
      const x = a[prop];
      const y = b[prop];
      return (x === y) ? 0 : (x < y) ? -1 * m : 1 * m;
    });
  }
}



